<template>
    <div class="singer_detail_album">
        <AlbumList :albumList="albumList" />
        <Paging :index="index" :total="total" :callBack="totalSwitch" />
    </div>
</template>

<script>
import AlbumList from "../components/AlbumList.vue";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return  {
            id: 0,
            // 
            rn: 30,

            // 
            index: 1,
            // 
            total: 0,

            // 
            albumList: [],
            // 
            albumBoo: true
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        
        // 
        this.setAlbumList(this.id, this.index, this.rn);
    },
    methods: {
        // 
        setAlbumList(id, pn, rn) {
            // 
            this.albumBoo = false;
            // 
            window.scrollTo(0, 0);
            
            var url = `/kuwo/api/www/artist/artistAlbum?artistid=${id}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.albumList = data.data.albumList;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
                // 
                this.albumBoo = true;
            } )
        },
        // 
        totalSwitch(total) {
            this.index = total;

            // 
            this.setAlbumList(this.id, this.index, this.rn);
        }
    },
    components: {
        Paging,
        AlbumList
    }
}
</script>