<template>
    <div class="singer_detail_mv">
        <MvList :mvList="mvList" />
        <Paging :index="index" :total="total" :callBack="totalSwitch" />
    </div>
</template>

<script>
import MvList from "../components/MvList.vue";
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return  {
            id: 0,
            // 
            rn: 30,

            // 
            index: 1,
            // 
            total: 0,

            // 
            mvList: [],
            // 
            mvBoo: true
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        
        // 
        this.setMvList(this.id, this.index, this.rn);
    },
    methods: {
        // 
        setMvList(id, pn, rn) {
            // 
            this.mvBoo = false;
            // 
            window.scrollTo(0, 0);
            
            var url = `/kuwo/api/www/artist/artistMv?artistid=${id}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.mvList = data.data.mvlist;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
                // 
                this.mvBoo = true;
            } )
        },
        // 
        totalSwitch(total) {
            this.index = total;

            // 
            this.setMvList(this.id, this.index, this.rn);
        }
    },
    components: {
        Paging,
        MvList
    }
}
</script>