<template>
    <div class="search">
        <div class="inp">
            <div class="inp_con">
                <div class="inp_input">
                    <div class="i" @click="jvGai">
                        <input type="text" placeholder="/MV//" ref="inps">
                        <i class="souSuo-icon iconFont"></i>
                    </div>
                    <div class="xia" v-show="jv && !val">
                        <div class="title"></div>
                        <ul>
                            <li v-for="(item, i) in reSou" :key="i">
                                <router-link :to="`/kuwo/search/list?key=${item}`">
                                    <span class="list">{{+i + 1}}</span>
                                    <span class="text">{{item}}</span>
                                </router-link>
                            </li>
                        </ul>
                    </div>
                    <div class="ti" v-show="jv && val">
                        <ul>
                            <li v-for="(item, i) in sou" :key="i">
                                <router-link :to="`/kuwo/search/list?key=${fenXiData(item)}`">
                                    <span class="text">{{fenXiData(item)}}</span>
                                </router-link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <div class="tabs">
            <span></span>
            <ul>
                <li><router-link :to='`/kuwo/search/list?key=${val}`'></router-link></li>
                <li><router-link :to='`/kuwo/search/album?key=${val}`'></router-link></li>
                <li><router-link :to='`/kuwo/search/mv?key=${val}`'>MV</router-link></li>
                <li><router-link :to='`/kuwo/search/playlist?key=${val}`'></router-link></li>
                <li><router-link :to='`/kuwo/search/singers?key=${val}`'></router-link></li>
            </ul>
        </div>
        <router-view />
    </div>
</template>

<script>
import tool from "../static/js/tool"
export default {
    data() {
        return {
            jv: false,
            val: null,
            reSou: [],
            sou: []
        }
    },
    methods: {
        jvGai(e) {
            this.jv = true;
            e.stopPropagation();
        },
        setReSou() {
            tool.ajax('/kuwo/api/www/search/searchKey?key=')
            .then( (data) => {
                this.reSou = data.data;
            } )
        },
        fenXiData(data) {
            let str = data.split(`\n`)[0];
            str = str.split("=")[1];
            if( typeof str !== "string" ) {
                str = "";
            }
            return str.trim();
        }
    },
    computed: {
    },
    mounted() {
        window.addEventListener("click", () => {
            this.jv = false;
        }, false)
        // 
        this.setReSou();
        // input
        this.$refs.inps.oninput = (e) => {
            this.val = e.target.value;
            tool.ajax(`/kuwo/api/www/search/searchKey?key=${this.val}`)
            .then( (data) => {
                this.sou = data.data;
            } )
        }

        // 
        this.val = this.$router.history.current.query.key;
        this.$refs.inps.value = this.val;
        if(this.val) {
            tool.ajax(`/kuwo/api/www/search/searchKey?key=${this.val}`)
            .then( (data) => {
                this.sou = data.data;
            } )
        }
    }
}
</script>