<template>
    <div class="play_detail">
        <div class="content clearBoth">
            <div class="content_left">
                <img :src="musicData.albumpic" alt="">
                <p class="intr"></p>
                <p class="intr_text" :class="!zhan ? 'xian' : ''">{{musicData.albuminfo}}</p>
                <p class="zhan" v-show="!zhan" @click="zhanKai"></p>
                <p class="shou" v-show="zhan" @click="shouQi"></p>
            </div>
            <div class="content_right">
                <p data-v-600ec1be="" class="song_name flex_c">
                    <span data-v-600ec1be="" class="name">{{musicData.name}}</span>
                </p>
                <p data-v-600ec1be="" class="artist_name flex_c">
                    <span data-v-600ec1be="" class="name">{{musicData.artist}}</span>
                </p>
                <p data-v-600ec1be="" class="song_info">
                    <span data-v-600ec1be=""></span> 
                    <span data-v-600ec1be="" class="tip album_name">{{musicData.album}}</span> 
                    <span data-v-600ec1be=""></span> 
                    <span data-v-600ec1be="" class="time">{{musicData.releaseDate}}</span>
                </p>
                <div data-v-600ec1be="" class="btns">
                    <button data-v-600ec1be="" class="p bg_primary"
                        @click="musicPlay">
                        <span data-v-600ec1be=""></span>
                    </button> 
                    <button data-v-600ec1be="">
                        <span data-v-600ec1be=""></span>
                    </button> 
                    <button data-v-600ec1be="">
                        <span data-v-600ec1be=""></span>
                    </button> 
                    <button data-v-600ec1be="">
                        <span data-v-600ec1be=""></span>
                    </button> 
                    <button data-v-600ec1be="">
                        <span data-v-600ec1be=""></span>
                    </button>
                </div>
                <div class="lrc">
                    <ul :style="[sty]">
                        <li v-for="(item, index) in lrc" :key="index"
                            :class="index == lrcIndex ? 'active': ''">{{item.lineLyric}}</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import tool from "../static/js/tool";
export default {
    data() {
        return {
            musicData: {},
            lrc: [],
            lrcIndex: 0,
            id: 0,
            music: new Audio(),
            zhan: false
        }
    },
    computed: {
        sty() {
            if( this.lrcIndex < 6) {
                return {
                    top: "0px",
                }
            }
            return {
                top: -(this.lrcIndex - 6) * 24 + "px",
            }
        }
    },
    methods: {
        // 
        setMusicXin(id) {
            tool.ajax(`/kuwo/api/www/music/musicInfo?mid=${id}`)
            .then( (data) => {
                this.musicData = data.data;
            } )
        },
        // 
        setLrc(id) {
            tool.ajax(`/kuwo/newh5/singles/songinfoandlrc?musicId=${id}`)
            .then( (data) => {
                this.lrc = data.data.lrclist;
            } )
        },
        // 
        musicPlay() {
            tool.ajax(`/kuwo/url?format=mp3&rid=${this.id}&response=url&type=convert_url3&br=128kmp3&from=web&t=1587030546976`)
            .then( (data) => {
                const url = data.url;
                this.music.src = url;
                // 
                this.baoMusicData(this.musicData);
                // 
                this.lrcIndex = 0;
                this.bus.$emit("music", this.musicData, this.music, (time) => {
                    // 
                    if(this.lrc[this.lrcIndex + 1] && this.lrc[this.lrcIndex + 1].time <= time) {
                        this.lrcIndex += 1;
                    }
                });
            } )
        },
        //  
        baoMusicData(musicData) {
            // 
            const data = window.localStorage.getItem('songList');
            if(!data) {
                window.localStorage.setItem('songList', JSON.stringify([musicData]));
            }
            // 
            else {
                // 
                const d = JSON.parse(data);
                for(let i = 0; i < d.length; i ++) {
                    if(musicData.rid == d[i].rid) {
                        d.splice(i, 1);
                        i --;
                    }
                }
                // 
                d.unshift(musicData);
                window.localStorage.setItem('songList', JSON.stringify(d));
                
                console.log(d);
            }
        },

        // 
        zhanKai() {
            this.zhan = true;
        },
        shouQi() {
            this.zhan = false;
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        // 
        this.setMusicXin(this.id);
        // 
        this.setLrc(this.id);
        // 
        window.scrollX = 0;
    }
}


