<template>
    <div class="singer_detail_list">
        <ul class="title">
            <li data-v-edb94b72="" class="head_num"></li>
            <li data-v-edb94b72="" class="head_name"></li> 
            <li data-v-edb94b72="" class="head_album"></li>
            <li data-v-edb94b72="" class="head_time"></li>
        </ul>
        <ul class="singer_list" v-show="musicBoo">
            <li v-for="(item, i) in musicAgg" :key="item.rid">
                <router-link :to="`/kuwo/play_detail/${item.rid}`">
                    <ul>
                        <li data-v-edb94b72="" class="head_num">
                            <span>{{(index - 1) * rn + i}}</span>
                            <img :src="item.albumpic" alt="">
                        </li>
                        <li data-v-edb94b72="" class="head_name">
                            <span>{{item.name || " "}}</span>
                            <router-link :to="`/kuwo/mv/mvplay/${item.rid}`" v-show="item.hasmv == 1">MV</router-link>
                        </li> 
                        <li data-v-edb94b72="" class="head_album">{{item.album || " "}}</li>
                        <li data-v-edb94b72="" class="head_time">{{item.songTimeMinutes || " "}}</li>
                    </ul>
                </router-link>
            </li>
        </ul>
        <div class="png">
            <Paging :index="index" :total="total" :callBack="totalSwitch" />
        </div>
    </div>
</template>

<script>
import Paging from "../components/Paging.vue";
import tool from "../static/js/tool";
export default {
    data() {
        return  {
            id: 0,
            // 
            rn: 30,

            // 
            index: 1,
            // 
            total: 0,

            // 
            musicAgg: [],
            // 
            musicBoo: true
        }
    },
    mounted() {
        this.id = this.$router.history.current.params.id;
        
        // 
        this.setMusicAgg(this.id, this.index, this.rn);
    },
    methods: {
        // 
        setMusicAgg(id, pn, rn) {
            // 
            this.musicBoo = false;
            // 
            window.scrollTo(0, 0);


            var url = `/kuwo/api/www/artist/artistMusic?artistid=${id}&pn=${pn}&rn=${rn}`;
            
            tool.ajax(url)
            .then( (data) => {
                this.musicAgg = data.data.list;
                this.total = parseInt(data.data.total / rn);
                if(this.total * rn < data.data.total) this.total += 1;
                
                // 
                this.musicBoo = true;
            } )
        },
        // 
        totalSwitch(total) {
            this.index = total;

            // 
            this.setMusicAgg(this.id, this.index, this.rn);
        }
    },
    components: {
        Paging
    }
}
</script>